body{
  background-color: rgb(0, 0, 0);
}
h1{
  font-size: 38px;
  
}
.main_heading{
  font-size: 40px;
  color: #ddd;
}
.heading{
  height:70px;
  margin:0 0 0 0;
  background: #8f13fd;
  color:white;
  font-weight: 900;
}
input[type=text] {
  width: 30%;
  height: 125px;
  padding: 12px 40px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-radius: 15px;
  background-color: rgb(197, 193, 193);
  color: rgb(10, 10, 10);
  font-family: Arial, Helvetica, sans-serif;
  position: fixed;
  left: 0;
  bottom: 130px;
  width: 100%;
  color:black;
  text-align: center;
  outline: none;
  box-shadow: 2px 2px 5px white;
}


.button {
  height: 70px;
  width:70px;
  background-color:  #8f13fd;
  color: #ddd;
  padding: 5px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 2px 273px;
  /* cursor: pointer; */
  border-radius: 56px;
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
  position: fixed;
  top:500px;
  box-shadow: 2px 2px 5px #8f13fd;
  outline: none;
  border: none !important;
 
}

.container{
  background-color: antiquewhite;
  border-style: hidden;
  border-radius: 15px;
  padding:18px;
  text-align: center;
  box-shadow: 0 8px 6px -6px black;
  
 
  
}
.data{
  height: 50%;
  color:white;
  list-style: none;
}

@media screen and (max-width:900px){

    .main-contianer{
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: space-evenly; 
      
    }
  
  }

